body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  padding: 0;
  margin: 0;
  font-family: 'Dokdo', cursive;
}

body {
  background-color: black;
}

#title {
  font-size: 150px;
  position: absolute;
  color: white;
  top: 20%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  -webkit-animation: title_anim 1s 1;
          animation: title_anim 1s 1;
}

#tic {
  -webkit-animation: pop_tic_anim 0.50s 1;
          animation: pop_tic_anim 0.50s 1;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

#tac {
  -webkit-animation: pop_tac_anim 0.50s 1;
          animation: pop_tac_anim 0.50s 1;
  -webkit-animation-delay: 1.5s;
          animation-delay: 1.5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

#toe {
  -webkit-animation: pop_toe_anim 0.50s 1;
          animation: pop_toe_anim 0.50s 1;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes title_anim {
  from {
    top: 50%;
  }
  to {
    top: 20%;
  }
}

@keyframes title_anim {
  from {
    top: 50%;
  }
  to {
    top: 20%;
  }
}

@-webkit-keyframes pop_tic_anim {
  25% {
    font-size: 150px;
  }
  50% {
    font-size: 200px;
    color: blue;
  }
  100% {
    font-size: 150px;
    text-shadow: 1px 1px 1px white;
    color:blue;
  }
}

@keyframes pop_tic_anim {
  25% {
    font-size: 150px;
  }
  50% {
    font-size: 200px;
    color: blue;
  }
  100% {
    font-size: 150px;
    text-shadow: 1px 1px 1px white;
    color:blue;
  }
}

@-webkit-keyframes pop_tac_anim {
  25% {
    font-size: 150px;
  }
  50% {
    font-size: 200px;
    color: gray;
  }
  100% {
    font-size: 150px;
    color: gray;
    text-shadow: 1px 1px 1px white;
  }
}

@keyframes pop_tac_anim {
  25% {
    font-size: 150px;
  }
  50% {
    font-size: 200px;
    color: gray;
  }
  100% {
    font-size: 150px;
    color: gray;
    text-shadow: 1px 1px 1px white;
  }
}

@-webkit-keyframes pop_toe_anim {
  25% {
    font-size: 150px;
  }
  50% {
    font-size: 200px;
    color: red;
  }
  100% {
    font-size: 150px;
    text-shadow: 1px 1px 1px white;
    color:red;
  }
}

@keyframes pop_toe_anim {
  25% {
    font-size: 150px;
  }
  50% {
    font-size: 200px;
    color: red;
  }
  100% {
    font-size: 150px;
    text-shadow: 1px 1px 1px white;
    color:red;
  }
}
